/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
.otp_main_box {
  margin: 0 16px;
  width: 100%;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  background-color: #fff;
  width: 400px;
  overflow: hidden;
  border-radius: 3px;
  position: relative;
  height: 600px;
  padding: 16px 20px;

  .btnBack_otp_svg {
    display: flex;
    .svg_back_Box {
      display: flex;
   
    }
  }
  .form_box_otp {
    padding: 0;
    width: 100%;
    height: 100%;
    min-height: 250px;
    border-radius: 3px;
    box-sizing: border-box;

    .form_container_otp {
      .lst_otp_1 {
        display: flex;
        margin: 20px 0px 16px;
        text-align: center;
        align-items: center;
        justify-content: center;
      }
      .lst_otp_2 {
        padding: 0;
        text-align: center;
        color: #002f34;
        margin: 0;
        overflow: hidden;
        font-size: 20px;
        font-weight: 700;

        margin-block-end: 15px;
      }
      .lst_otp_3 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 20px px 8px 0;
        padding: 0;
        text-align: center;
        color: #002f34;
        font-size: 14px;
        
      }
      .lst_otp_4 {
        min-height: 90px;
        display: flex;
        flex-direction: column;
        margin: 20px 0;
        align-items: center;
        position: relative;
        .box_input {
          direction: ltr;
          min-height: 88px;
          position: relative;
          .input_container_otp {
            width: 100%;
            height: 60px;
            display: flex;
            .box_1_otp {
              height: 100%;
              margin: 0px 8px;
              position: relative;
              box-sizing: border-box;

              input {
                padding: 8px 0px;
                box-sizing: border-box;
                z-index: 2;
                width: 100%;
                border: none;
                height: 100%;
                outline: none;
                background: none;
                font-size: 24px;
                font-weight: 700;
                position: relative;
                text-align: center;
                font-style: normal;
                font-stretch: normal;
                color: rgb(0, 47, 52);
                box-shadow: rgb(0 47 52 / 64%) 0px 0px 0px 1px inset;
                
              }
              .box_inside_line_otp {
                left: 0px;
                top: 8px;
                z-index: 1;
                opacity: 1;
                width: 100%;
                font-size: 45px;
                font-weight: 700;
                text-align: center;
                position: absolute;
                visibility: visible;
                color: rgb(0, 47, 52);
                transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
              }
            }
          }
        }
      }
    }
    .lst_otp_5 {
      span {
        height: 30px;
        margin: 8px 0px;
        display: block;
        cursor: pointer;
        color: rgb(58, 119, 255);
        font-size: 12px;
        font-weight: 700;
        text-transform: none;
      }
    }
  }
}
